import sTabs from '@/s-ui/s-tabs';
import sTab from '@/s-ui/s-tab';
const util = require('../../static/util/util.js');
import refresh from '../../components/refresh.vue';
import navTab from '../../components/navTab.vue';
import tabBar4 from '../../components/tabBar4.vue';


export default {
	components: {
		refresh,
		navTab,
		tabBar4
	},
	components: {
		sTabs,
		sTab
	},
	data() {
		return {
			activeTab: 0,
			detailsList: [],
			rechargedList: [],
			ishidden : false,
			currentPage: 'index',
			toView: '', //回到顶部id
			tabTitle: ['交易明细', '充值记录'], //导航栏格式 --导航栏组件
			currentTab: 0, //sweiper所在页
			pages: [1, 1], //第几个swiper的第几页
			list: [
				[],
				[]
			]
		};
	},
	onShow() {
		let list1 = [{
			    costType: '检测费用',
				inscost: 10,
				payType: '余额支付',
				spend: -10,
				paytime: '2020-02-27',
				balance: 10
			},
			{
				costType: '检测费用',
				inscost: 11,
				payType: '余额支付',
				spend: -10,
				paytime: '2020-02-27',
				balance: 10
			},
			{
				costType: '检测费用',
				inscost: 12,
				payType: '余额支付',
				spend: -10,
				paytime: '2020-02-27',
				balance: 10
			},
			{
				costType: '检测费用',
				inscost: 13,
				payType: '余额支付',
				spend: -10,
				paytime: '2020-02-27',
				balance: 10
			},
			{
				costType: '余额充值',
				inscost: 14,
				payType: '微信支付',
				spend: -10,
				paytime: '2020-02-27',
				balance: 10,
				giftAmount: 10
			}
		]
		this.list[0] = list1
		for(let i = 0;i<list1.length;i++){
			if( list1[i].costType === '余额充值' ){
				this.list[1].push(list1[i])
			}
		}

	},
	methods: {
		change(index) {
			if(index === '0'){
				this.ishidden = true
			}else{
				this.ishidden = false
			}
			
			console.log(index);
		},
		toTop() {
			this.toView = ''
			setTimeout(() => {
				this.toView = 'top' + this.currentTab
			}, 10)
		},
		changeTab(index) {
			this.currentTab = index;
		},
		// swiper 滑动
		swiperTab: function(e) {
			var index = e.detail.current //获取索引
			this.$refs.navTab.longClick(index);
		},
		// 加载更多 util.throttle为防抖函数
		lower1: util.throttle(function(e) {
		}, 300),
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e);
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
		isRefresh() {
			setTimeout(() => {
				uni.showToast({
					icon: 'success',
					title: '刷新成功'
				})
				this.$refs.refresh.endAfter() //刷新结束调用
			}, 1000)
		}
	}
};
